/* Component Style */
.circleContainer {
  width: 180px;
  height: 180px;
  position: relative;
}
.circleLoaderWrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  overflow: hidden;
}
.circleLoader {
  display: block;
  position: relative;
  left: 50%;
  top: 50%;
  width: 120px;
  height: 120px;
  margin: -75px 0 0 -75px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #b30000;
  animation: spin 1.7s linear infinite;
  z-index: 11;
}
.circleLoader::before {
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #510aac;
  animation: spin-reverse 0.6s linear infinite;
}
.circleLoader::after {
  content: '';
  position: absolute;
  top: 25px;
  left: 25px;
  right: 25px;
  bottom: 25px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #96989c;
  animation: spin 1s linear infinite;
}
.circleLoaderWrapper > .circleLoaderSection > .sectionLeft {
  left: 0;
}
.circleLoaderWrapper > .circleLoaderSection > .sectionRight {
  right: 0;
}
.loaded > .circleLoaderWrapper > .circleLoaderSection > .sectionLeft {
  transform: translateX(-100%);
  transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.loaded > .circleLoaderWrapper > .circleLoaderSection > .sectionRight {
  transform: translateX(100%);
  transition: all 0.7s 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.loaded > .circleLoader {
  opacity: 0;
  transition: all 0.3s ease-out;
}
.loaded > .circleLoaderWrapper {
  visibility: hidden;
  transform: translateY(-100%);
  transition: all 0.3s 1s ease-out;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes spin-reverse {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}
