/* Component Style */
.errorBoundContainer {
  width: 360px;
  height: 360px;
  padding: 16px;
  bottom: 24px;
  right: 24px;
  position: fixed;
  background: white;
  z-index: 10000000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: auto;
  border-radius: 4px;
  box-shadow: 0px 4px 8px 0px rgb(0 0 0 / 16%), 0px 0px 0px 0px rgb(0 0 0 / 0%);
}
.errorBoundTitleStyle {
  text-align: center;
  font-size: 14px;
  margin: 0px;
  padding: 0px;
}
.errorBoundNameStyle {
  font-size: 12px;
  margin: 0px;
  padding: 0px;
  margin-top: 12px;
}
.errorBoundContentStyle {
  font-size: 16px;
  white-space: pre-wrap;
  margin: 0px;
  padding: 0px;
  margin-top: 12px;
}
.errorBoundContent1Style {
  font-size: 16px;
  white-space: pre-wrap;
  margin: 0px;
  padding: 0px;
}
